import * as React from 'react';
import { render } from '@testing-library/react';
import Zhi from './reader';
import formula from './fixtures/formula.json';

describe('Zhi:formula', () => {
  it('正确渲染', () => {
    const asl = [
      'root',
      [
        'p',
        [
          'span',
          {
            'data-type': 'text',
          },
          [
            'span',
            {
              'data-type': 'leaf',
            },
            'This is paragraph.',
          ],
        ],
        formula,
        [
          'span',
          {
            'data-type': 'text',
          },
          [
            'span',
            {
              'data-type': 'leaf',
            },
            '',
          ],
        ],
      ],
    ];
    const container = render(<Zhi content={asl} />);
    expect(!!container.getByTestId('formula')).toBe(true);
  });
});